import "./app.less";

import appConfig from './app.config';
import BroadcastBuilder from './BroadcastBuilder/broadcastBuilder.module.js';

angular
	.module('app', [
		"vendor",
		BroadcastBuilder.name
	])

	.config(appConfig)

	.controller('RootCtrl', function () {
		var root = this;
		return root;
	})

	.directive("fileread", [function () {

		var returnFalse = function (scope) {
			scope.$apply(function () {
				scope.fileread = "";
			});
		};

		return {
			scope: {
				fileread: "="
			},
			link: function (scope, element, ) {
				element.bind("change", function (changeEvent) {
					if (!changeEvent ||
						!changeEvent.target ||
						!changeEvent.target.files ||
						!changeEvent.target.files[0] ) {
						return returnFalse(scope);
					}

					var reader = new FileReader();
					reader.onload = function (loadEvent) {
						scope.$apply(function () {
							scope.fileread = loadEvent.target.result;
						});
					};
					reader.onerror = function (error) {
						console.warn('FileReader error:', error);
					};
					reader.readAsDataURL(changeEvent.target.files[0]);
				});
			}
		}
	}])
	.filter('unsafe', ['$sce', function ($sce) {
		return function (val) {
			return $sce.trustAsHtml(val);
		};
	}])
	.directive('contenteditable',function ( $sce ) {
		return {
			require: "?ngModel",
			restrict: "A",

			link: function(scope, $el, attrs, ngModel) {
				if (!ngModel) return; // do nothing if no ng-model

				// Specify how UI should be updated
				ngModel.$render = function() {
					$el.html($sce.getTrustedHtml(ngModel.$viewValue || ''));
				};

				// Listen for change events to enable binding
				$el.on('blur keyup change', function() {
					scope.$evalAsync(read);
				});

				$el.on("focus", () => {
					let placeHolderValue = ( attrs.placeholder || "" ).trim();
					if ($el.text().trim() === placeHolderValue) {
						$el.text("");
					}
				});
				$el.on("blur", () => {
					let placeHolderValue = ( attrs.placeholder || "" ).trim();
					if (!$el.text().trim()) {
						setPlaceholder(placeHolderValue);
					}
				});

				setPlaceholder(attrs.placeholder);

				// read(); // initialize

				// Write data to the model
				function read() {
					var html = $el.html();
					// When we clear the content editable the browser leaves a <br> behind
					// If strip-br attribute is provided then we strip this out
					if (attrs.stripBr && html === '<br>') {
						html = '';
					}
					ngModel.$setViewValue(html);
				}

				function setPlaceholder (text="") {
					// TODO refactor this, make it in separate div or in :after block

					let placeHolderValue = `<div class="s-c-gray-light">${text}</div>`;
					$el.html(placeHolderValue);
				}
			}
		}
	})

	.directive('maxLength',function ($compile) {
		return {
			restrict: "A",
			require: "?ngModel",
			link: function (scope,$el, attrs) {
				let maxLength = attrs.maxLength;

				let ngExpression = `${maxLength}-${attrs.ngModel}.length`;

				scope.getColor = (val) => {
					if (val > 0) {
						return "green";
					} else {
						return "red";
					}
				};

				var content = `<div max-length-value ng-style="{color: getColor(${ngExpression}) }">{{ ${ngExpression} }}</div>`;
				var compiled = $compile(content);
				$el.after(compiled(scope));
			}
		}
	})

	.directive('willFocus',function ($timeout) {
		return {
			restrict: "A",
			link: function ($scope,$el,attrs) {
				let willFocusThrow = attrs.willFocus || 200;
				let el = $el[0];
				console.warn('willFocus',$el,el);
				$timeout(()=>{
					try {
						el.focus();
					} catch (err) {
						console.warn('willFocus fails',err);
					}
				},willFocusThrow)
			}
		}
	})


	.run(function ($rootScope) {

		$rootScope.$on('$stateChangeStart', console.info.bind(console, "$stateChangeStart:") );
		$rootScope.$on('$stateChangeSuccess', console.info.bind(console, "$stateChangeSuccess:") );
		$rootScope.$on('$stateChangeError', console.warn.bind(console, "$stateChangeError:") );
		$rootScope.$on('$stateNotFound', console.warn.bind(console, "$stateNotFound:") );
	})
;



