export default function ($stateProvider) {

	const BASE_URL = ""; // base url
	const url = (url) => { // base url
		return `${BASE_URL}${url}`
	};

	$stateProvider
	.state("broadcast",{
		url: url('/{id}'),
		data: {
			title: "Me broadcast"
		},
		template: '<broadcast-builder queue="vm.messagesQueue"></broadcast-builder>',
		controller: function (messagesQueue) {
			let vm = this;
			vm.messagesQueue = messagesQueue;
		},
		controllerAs: "vm",
		params: {
			id: {
				value: "0",
				squash: true
			}
		},
		resolve: {
			messagesQueue: ($stateParams,$state, BroadcastQueuesService) => {
				let id = +$stateParams.id;
				let queue = BroadcastQueuesService.getById(id) || BroadcastQueuesService.addQueue();
				return queue;
			}
		},
		onEnter:($stateParams,$state) => {
			console.warn(`state '${$state.current.name}'`,$stateParams);
			return {};
		}
	});


	// .state("/.child", {
	// 	url: url('{id}'),
	// 	template: '<broadcast-builder></broadcast-builder>',
	// 	onEnter:($stateParams) => {
	// 		console.warn('childBranch person resolve',$stateParams);
	// 		return {};
	// 	}
	// });
}



